<template lang="pug">
.progress-bar
    span(:style='progress.textColor', v-text='progress.num')
    svg(id='progress', viewPort="0 0 100 100", xmlns="http://www.w3.org/2000/svg", xmlns:sketch="http://www.bohemiancoding.com/sketch/ns", xmlns:xlink="http://www.w3.org/1999/xlink")
        circle(r='90', cx='100', cy='100')
        circle#bar(:style="progress.styleObj", transform="rotate(-90 100 100)", r='90', cx='100', cy='100')
</template>

<style lang="scss">
@import '../../../sass/_base.scss';

$scale: 1;
$svg-width: 200px;
$progress-bg-color: rgba(0, 0, 10, 0.3);
$stroke-width: 20px;

@keyframes textOpacity {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

.progress-bar {
    margin: 10px 20px;
    width: $svg-width;
    transform: scale($scale);
    text-align: center;
    line-height: 200px;
    font-size: 2em;
    font-weight: bold;
    font-family: "Times New Roman", Georgia, Serif;
    animation: textOpacity 1.2s linear;
    border-radius: 50%;
    box-shadow: 0 0 20px 5px rgba(0, 0, 10, 0.5);
    span {
        text-shadow: 0 0 1px #000;
    }
    #progress {
        position: absolute;
        left: 0;
        @include square($svg-width);
        circle {
            fill: transparent;
            stroke-dasharray: 565.48;
            stroke-dashoffset: 0;
            transition: stroke-dashoffset 1.2s linear;
            stroke: $progress-bg-color;
            stroke-width: $stroke-width;
        }
    }
}
</style>

<script>
export default {
    name: 'progressbar',
    props: ['progressnum'],
    computed: {
        progress () {
            let n = this.progressnum
            if (isNaN(n)) {
                return 100
            } else {
                let r = 90
                let c = Math.PI * (r * 2)
                if (n < 0) {
                    n = 0
                }
                if (n > 100) {
                    n = 100
                }
                let pct = ((100 - n) / 100) * c
                // console.log(pct)
                return {
                    styleObj: {
                        strokeDashoffset: `${pct}px`,
                        stroke: `hsla(${this.progressnum}, 80%, 50%, .7)`
                    },
                    num: `${n}%`,
                    textColor: `color: hsla(${this.progressnum}, 80%, 30%, .9)`
                }
            }
        }
    }
}
</script>



