<template lang="pug">
li.todo(:class='{completed: todo.done, editing: editing}', @dblclick='editing = true')
    label.todo-chk(:for='chkId')
        svg(width='30px', mlns="http://www.w3.org/2000/svg", viewBox="0 0 200 200")
            circle.circle(fill='none', stroke='rgb(245, 183, 0)', storke-width='10', stroke-miterlimit="10", cx="100", cy="100", r="80")
            path.ok(:class='{chked: todo.done}' d="M116.158,29.336l-4.975-4.975c-3.469-3.469-9.088-3.478-12.549-0.019L48.103,74.875L29.364,56.136  c-3.459-3.46-9.078-3.45-12.549,0.021l-4.974,4.974c-3.47,3.47-3.48,9.089-0.02,12.549L41.8,103.657  c1.741,1.741,4.026,2.602,6.31,2.588c2.279,0.011,4.559-0.852,6.297-2.59l61.771-61.771  C119.637,38.424,119.631,32.807,116.158,29.336z", fill="#fff")
    input(:id='chkId', type='checkbox', :checked='todo.done', @change='toggleTodo({todo: todo})')
    span.num(v-text='i')
    label.text(v-text="todo.text")
    .del(@click='delTodo({todo: todo})')
        i.fa.fa-trash-o
    textarea.todo-edit(v-show='editing', v-focus='editing', :value='todo.text', @keyup.enter='doneEdit', @keyup.esc='cancelEdit', @blur='doneEdit')
</template>

<script>
import {mapMutations} from 'vuex'

export default {
    name: 'todo',
    props: ['todo', 'index'],
    data () {
        return {
            editing: false,
            i: `${this.index + 1}. `,
            chkId: `chk${this.index + 1}`
        }
    },
    directives: {
        focus (el, {value}, {context}) {
            if (value) {
                context.$nextTick(() => {
                    el.focus()
                })
            }
        }
    },
    methods: {
        ...mapMutations([
            'editTodo',
            'toggleTodo',
            'delTodo'
        ]),
        doneEdit (e) {
            let value = e.target.value.trim()
            let {todo} = this

            if (!value) {
                this.delTodo({todo})
            } else if (this.editing) {
                this.editTodo({
                    todo,
                    value
                })
                this.editing = false
            }
        },
        cancelEdit (e) {
            e.target.value = this.todo.text
            this.editing = false
        }
    }
}
</script>



<style lang="scss">
@import '../sass/_todo.list';
</style>



