<template lang="pug">
#root-todo
    header.todo-logo
        h1.logo
            a(href='/todo')
                i.fa.fa-list-ol
                | TODO
        a(href='Mailto:cxin1314@gmail.com?Subject=TODO', :title='tip.email').email
            i.fa.fa-envelope
            | ・Len

    section
        .todo-head
            input.new-todo(autofocus, autocomplete='off', :placeholder='msg', @keyup.enter='addTodo')
            i.icon-enter(:title='tip.enter')
            
        .todo-main
            p.all(v-if="todos.length")
                label.chkbox(:class='{on: allChecked, focus: allChecked}', for='allchk')
                    span.yes
                    span.no
                    span.toggle-all
                input#allchk(type='checkbox', :checked='allChecked', @change='toggleAll({done: !allChecked})')
                |  All completed / All unfinished
            ul.todo-list
                todo(v-for='(todo, index) in todosFiltered', :todo='todo', :index='index')

        .todo-foot(v-if="todos.length")
            p.todo-count
                span undone: 
                b(v-if="remaining !== 0") {{remaining}} {{remaining | pluralize('item')}}
                b(v-else) 0
            p.progress
                span Completed progress: 
                progressbar(:progressnum='(((all - remaining) / all) * 100).toFixed(2)')
            ul.filter
                li(v-for='(val, key) in filtersItem')
                    a(:href="'#/' + key", @click='visibility = key') {{ key | capitalize }}
            button.clear-completed(v-show='todos.length > remaining', @click='clearCompleted') Clear completed
    footer.todo-footer
        p Copyright © 2017
            a(href='https://github.com/lencx')
                i.fa.fa-github 
                |  lencx
</template>

<script>
import {mapMutations} from 'vuex'
import todo from './components/todo-item'
import progressbar from './components/progress-bar'

const filtersItem = {
    all: todos => todos,
    active: todos => todos.filter(todo => !todo.done),
    completed: todos => todos.filter(todo => todo.done)
}
export default {
    components: {
        todo,
        progressbar
    },
    data () {
        return {
            msg: 'What needs to be done?',
            tip: {
                email: 'Email',
                enter: 'add new TODO : ↵'
            },
            visibility: 'all',
            filtersItem,
            num: 29
        }
    },
    computed: {
        todos () {
            return this.$store.state.todos
        },
        todosFiltered () {
            return filtersItem[this.visibility](this.todos)
        },
        allChecked () {
            return this.todos.every(todo => todo.done)
        },
        remaining () {
            return this.todos.filter(todo => !todo.done).length
        },
        all () {
            return this.todos.filter(todos => todos).length
        }
    },
    methods: {
        addTodo (e) {
            // let text = e.currentTarget.value
            let text = e.target.value
            if (text.trim()) {
                this.$store.commit('addTodo', {text})
            }
            e.target.value = ''
        },
        ...mapMutations([
            'toggleAll',
            'clearCompleted'
        ])
    },
    filters: {
        pluralize: (num, word) => num === 1 ? word : word + 's',
        capitalize: s => s.charAt(0).toUpperCase() + s.slice(1)
    }
}
</script>

<style lang="scss">
@import './sass/_todo';
</style>



