// https://vuex.vuejs.org/en/
import Vue from 'vue'
import Vuex from 'vuex'
import {state, mutations} from './todo.mutate'
import plugins from './todo.plugin'

Vue.use(Vuex)

export default new Vuex.Store({
    state,
    mutations,
    plugins
})



